$fonts-path: '../fonts/';

@font-face {
  // lcdnova
  font-family: 'lcdnova';
  src:
  url($fonts-path + 'lcdnova.eot'),
  url($fonts-path + 'lcdnova.ttf') format('truetype'),
  url($fonts-path + 'lcdnova.woff2') format('woff2'),
  url($fonts-path + 'lcdnova.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

.sh_puzzle{
	-webkit-tap-highlight-color: rgba(0,0,0,0);
	display: inline-block;
	overflow: visible;
	position: relative;
	padding: 0;
}

.sp_bg{
  position: absolute;
  overflow: hidden;
  transition: opacity 0.1s linear;
  background: rgba(0, 0, 0, 0.0);
  background-color: rgba(0, 0, 0,0) !important;
  border-radius: 25px;

}

.sp_bg.visible{
  opacity: 1;
  transition-delay: 0s;
}

.spmenu,
.spmenu ul,
.spmenu ul li,
.spmenu #menu-button {
	margin: 0;
	padding: 0;
	border: 0;
	list-style: none;
	line-height: 1;
	display: block;
	position: relative;
	box-sizing: border-box;
}
.spmenu #menu-button {
	display: none;
}
.spmenu {
	width: auto;
	font-family: 'Arial', sans-serif;
	line-height: 1;
	box-shadow: 0 0 16px rgba(0, 0, 0, 0.5);
}
#menu-line {
	position: absolute;
	top: 0;
	left: 0;
	height: 3px;
	background: #009ae1;
	transition: all 0.25s ease-out;
}
.spmenu > ul > li {
	float: left;
	cursor: default;
}
.spmenu > ul > li {
	padding: 8px;
	font-size: 12px;
	text-decoration: none;
	text-transform: uppercase;
	color: #000;
	background-color: #fff;
	transition: color .2s ease;
}

.spmenu > ul > li:not(:last-child) {
	border-right: 1px solid #aaa;
}
.spmenu > ul > li:hover{
	color: #DDD;
	background-color: #333;
}
.spmenu ul ul {
	position: absolute;
	padding-top: 8px;
	left: -9999px;
	margin-left: -8px;
}
.spmenu li:hover > ul {
	left: auto;
}
.spmenu ul ul ul {
	margin-left: 100%;
	top: 0;
}
.spmenu ul li:hover > ul > li {
	height: 26px;
}
.spmenu ul ul li {
	white-space: nowrap;
	padding: 8px;
	font-size: 12px;
	background: #333;
	text-decoration: none;
	color: #dddddd;
	transition: color .2s ease;
}

.spmenu ul ul li:hover {
	color: #ffffff;
	background-color: #000;
}

html, body{
  margin: 0;
  padding: 0;
  overflow: visible;
  // height: auto;
}

body {
  font-family: 'lcdnova', sans-serif;
}

.sp_box {
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility:hidden;
  transition:visibility 0s linear 0.5s, z-index 0s linear 0.5s, opacity 0.5s linear;
  background: rgba(0, 0, 0, 0.73);
  z-index: 0;
  position: absolute;
  z-index: 0;
}
.sp_box.visible {
  visibility:visible;
  opacity:1;
  transition-delay:0s;
  z-index: 9999;
}

.sp_box > div{
  display: table;
  width: 100%;
  height: 100%;
}
.sp_box > div > div > *{
  margin: 0;
}
.sp_box > div > div {
  color: #EBEBEB;
  font-family: 'Arial', sans-serif;
  font-size: 46px;
  line-height: 60px;
  font-weight: bold;
  text-align: center;         
  height:100%;      
  width: 100%;  
  display: table-cell;      
  vertical-align: middle;
}

.sp_box > div > div > .mini {
  font-size: 22px;
  line-height: 40px;
}

.sp_box .retry-button  {
  display: inline-block;
  background: #2F92A4;
  border-bottom: 6px solid #205B66;
  font-size: 22px;
  padding: 0 20px 5px;
  text-decoration: none;
  color: #FFFFFF;
  height: 40px;
  line-height: 40px;
  cursor: pointer;
  margin-left: 9px;
}

.counter{
  font-size: 25px;
  line-height: 25px;
}

#Timer {
  z-index: 999;
  position: absolute;
  width: 100%;
  right: 0;
  bottom: -53px !important;
}
.sp_box .retry-button:active {
    background: #205B66;
}
.c_movies {
  width: 120px;
  height: 44px;
  line-height: 44px;
  vertical-align: middle;
  font-family: 'lcdnova', sans-serif;
  font-weight: 300;
  color: #e72642;
  text-align: center;
  margin: 0 auto;
  font-size: 26px;
  letter-spacing: 1.5px;
  background: rgba(0,0,0,.8);
  border-radius: 5px;
  padding-top: 2px;
}

#puzzle {
  width: 310px !important;

  @media screen and (min-width: 480px) {
    width: 392px !important;
  }
}


